import React, { useEffect, useState } from "react";
import { Modal, Button } from "react-bootstrap";

import useForm from "../../hooks/useForm";
import "./Navbar.css";

import validator from "validator";

// Imagenes
import foto1 from "../../images/1.jpg";
import logo from "../../images/logo-UPS.png"

const NavbarContainer = ({ idioma, handleIdioma, sesionIniciada, handleSesionIniciada, setEmailSesion }) => {
  const [loadingLogin, setLoadingLogin] = useState(false);
  const [loadingRegister, setLoadingRegister] = useState(false);

  const [texto, setTexto] = useState({});
  const [registrarModal, setRegistrarModal] = useState(false);

  // contador de errores
  const [contador_Errores, setContador_Errores] = useState([0, 0, 0, 0, 0]);

  const [formValues, handleInputChange, reset] = useForm({
    email_login: "",
    password_login: "",
    email_register: "",
    password_register: "",
    email_register_confirm: "",
    password_register_confirm: "",
  });

  const { email_login, password_login, email_register, password_register, email_register_confirm, password_register_confirm } = formValues;

  // Poner en lower las letras del correo
  useEffect(() => {
    const target = {
      name: "email_login",
      value: email_login.toLowerCase(),
    };
    handleInputChange({ target });
  }, [email_login]);

  useEffect(() => {
    const target = {
      name: "email_register",
      value: email_register.toLowerCase(),
    };
    handleInputChange({ target });
  }, [email_register]);

  const [validatorLoginCorreo, setValidatorLoginCorreo] = useState(false);
  const [validatorLoginContrasenia, setValidatorLoginContrasenia] = useState(false);

  const [validatorRegistroCorreo, setValidatorRegistroCorreo] = useState(false);
  const [validatorRegistroContrasenia, setValidatorRegistroContrasenia] = useState(false);
  const [validatorRegistroConfirmCorreo, setValidatorRegistroConfirmCorreo] = useState(false);
  const [validatorRegistroConfirmContrasenia, setValidatorRegistroConfirmContrasenia] = useState(false);

  const handleRegistrarModalClose = () => {
    setValidatorRegistroCorreo(false);
    setValidatorRegistroContrasenia(false);
    setValidatorRegistroConfirmCorreo(false);
    setValidatorRegistroConfirmContrasenia(false);
    reset();
    setRegistrarModal(false);
  };
  const handleRegistrarModalShow = () => setRegistrarModal(true);

  const [loginModal, setLoginModal] = useState(false);
  const handleLoginModalClose = () => {
    setValidatorLoginCorreo(false);
    setValidatorLoginContrasenia(false);
    reset();
    setLoginModal(false);
  };
  const handleLoginModalShow = () => setLoginModal(true);

  const handleClickRegistro = async () => {
    let bandera = true;
    let contador_de_errores = [...contador_Errores];

    if (!validator.isEmail(email_register)) {
      setValidatorRegistroCorreo(true);
      contador_de_errores[0] = 1;
      bandera = false;
    } else {
      setValidatorRegistroCorreo(false);
    }
    if (!validator.isStrongPassword(password_register)) {
      setValidatorRegistroContrasenia(true);
      contador_de_errores[1] = 1;
      bandera = false;
    } else {
      setValidatorRegistroContrasenia(false);
    }
    if (email_register === email_register_confirm) {
      setValidatorRegistroConfirmCorreo(false);
    } else {
      setValidatorRegistroConfirmCorreo(true);
      contador_de_errores[2] = 1;
      bandera = false;
    }
    if (password_register === password_register_confirm) {
      setValidatorRegistroConfirmContrasenia(false);
    } else {
      setValidatorRegistroConfirmContrasenia(true);
      contador_de_errores[3] = 1;
      bandera = false;
    }

    if (bandera) {
      setLoadingRegister(true);
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ correo: email_register, contrasenia: password_register }),
      };
      const response = await fetch("https://yates-calidad-back.leonelparrales.com/reg", requestOptions);
      const data = await response.json();
      if (data[0]) {
        alert("Usuario creado satisfactoriamente!");

        const requestOptions_2 = {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ num: contador_de_errores.reduce((a, b) => a + b, 0) }),
        };
        fetch("https://yates-calidad-back.leonelparrales.com/ESignUp", requestOptions_2);
        contador_de_errores = [0, 0, 0, 0, 0];
        handleRegistrarModalClose();
      } else {
        alert("El usuario ya existe!");
        contador_de_errores[4] = 1;
      }
      reset();
      setLoadingRegister(false);
    }
    setContador_Errores(contador_de_errores);
  };

  const handleClickLogin = async () => {
    let bandera = true;
    if (!validator.isEmail(email_login)) {
      setValidatorLoginCorreo(true);
      bandera = false;
    } else {
      setValidatorLoginCorreo(false);
    }
    if (password_login.trim().length < 1) {
      setValidatorLoginContrasenia(true);
      bandera = false;
    } else {
      setValidatorLoginContrasenia(false);
    }
    if (bandera) {
      setLoadingLogin(true);
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ correo: email_login, contrasenia: password_login }),
      };
      const response = await fetch("https://yates-calidad-back.leonelparrales.com/login", requestOptions);
      const data = await response.json();
      if (!data[0]) {
        alert("Usuario o Contraseña Incorrecta");
      } else {
        handleSesionIniciada(true);
        setEmailSesion(email_login);
        handleLoginModalClose();
      }
      reset();
      setLoadingLogin(false);
    }
  };

  let estilosLoadingLogin1 = "";
  let estilosLoadingLogin2 = "outline-danger";
  let estilosLoadingLogin3 = "primary";

  if (loadingLogin) {
    estilosLoadingLogin1 += "disabled";
    estilosLoadingLogin2 += " disabled";
    estilosLoadingLogin3 += " disabled";
  }
  let estilosLoadingRegister1 = "";
  let estilosLoadingRegister2 = "outline-danger";
  let estilosLoadingRegister3 = "primary";

  if (loadingRegister) {
    estilosLoadingRegister1 += "disabled";
    estilosLoadingRegister2 += " disabled";
    estilosLoadingRegister3 += " disabled";
  }

  useEffect(() => {
    if (idioma == "En") {
      setTexto({
        buscar: "Search",
        iniciarSesion: "Sign in",
        cerrar: "Close",
        registrarse: "Sign up",
        email: "Email address",
        password: "Password",
        email_error: "Invalid email.",
        error_password: "Invalid password. Minimum 8 characters. Must include uppercase, lowercase, numbers and special characters.",
        error_password_2: "Enter a valid password.",
        cerrar_sesion: "Log Out",
        repita_correo: "Confirm email",
        repita_contrasenia: "Confirm password",
        error_confirm_1: "Email does not match",
        error_confirm_2: "Password does not match",
      });
    } else if (idioma == "Es") {
      setTexto({
        buscar: "Buscar",
        iniciarSesion: "Iniciar Sesión",
        cerrar: "Cerrar",
        registrarse: "Registrarse",
        email: "Correo electrónico",
        password: "Contraseña",
        email_error: "Correo electrónico inválido.",
        error_password: "Contraseña inválida. Mínimo 8 caracteres. Debe incluir maýusculas, minúsculas, números y caractéres especiales.",
        error_password_2: "Ingrese una contraseña válida.",
        cerrar_sesion: "Cerrar Sesión",
        repita_correo: "Confirmar correo electrónico",
        repita_contrasenia: "Confirmar contraseña",
        error_confirm_1: "El email no coincide",
        error_confirm_2: "Las contraseñas no coinciden",
      });
    } else if (idioma == "Rs") {
      setTexto({
        buscar: "Ищу",
        iniciarSesion: "Авторизоваться",
        cerrar: "Неисправность",
        registrarse: "зарегистрироваться",
        email: "Адрес электронной почты",
        password: "Пароль",
        email_error: "Неверный адрес электронной почты.",
        error_password: "Неправильный пароль. Минимум 8 символов. Должен включать прописные и строчные буквы, цифры и специальные символы.",
        error_password_2: "Введите действующий пароль.",
        cerrar_sesion: "Выйти",
        repita_correo: "Подтвердить Email",
        repita_contrasenia: "Подтвердить Пароль",
        error_confirm_1: "Email не соответствует",
        error_confirm_2: "Пароль не подходит",
      });
    } else {
      setTexto({
        buscar: "Wiii",
        iniciarSesion: "S'identifier",
        cerrar: "Fermer",
        registrarse: "S'inscrire",
        email: "Adresse e-mail",
        password: "Mot de passe",
        email_error: "Email invalide.",
        error_password: "Mot de passe incorrect. Au moins 8 caractères. Doit inclure des majuscules, des minuscules, des chiffres et des caractères spéciaux.",
        error_password_2: "Entrez un mot de passe valide.",
        cerrar_sesion: "Se déconnecter",
        repita_correo: "Confirmez votre e-mail",
        repita_contrasenia: "Confirmez le mot de passe",
        error_confirm_1: "Email ne correspond pas",
        error_confirm_2: "Le mot de passe ne correspond pas",
      });
    }
  }, [idioma]);

  return (
    <nav className="navbar navbar-light bg-light">
      <Modal show={loginModal} onHide={handleLoginModalClose} backdrop="static" keyboard={false}>
        <Modal.Header>
          <Modal.Title>{texto.iniciarSesion}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div class="form-floating">
            <input type="email" class="form-control" id="floatingInput" placeholder="name@example.com" name="email_login" value={email_login} onChange={handleInputChange} disabled={estilosLoadingLogin1} />
            <label for="floatingInput">{texto.email}</label>
          </div>
          {validatorLoginCorreo && <div class="navbar-error1">{texto.email_error}</div>}
          <div class="mb-3"></div>
          <div class="form-floating">
            <input type="password" class="form-control" id="floatingPassword" placeholder="Password" name="password_login" value={password_login} onChange={handleInputChange} disabled={estilosLoadingLogin1} />
            <label for="floatingPassword">{texto.password}</label>
          </div>
          {validatorLoginContrasenia && <div class="navbar-error1">{texto.error_password_2}</div>}
          <div class="mb-3"></div>

          {loadingLogin && (
            <div class="spinner-border text-primary" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant={estilosLoadingLogin2} onClick={handleLoginModalClose}>
            {texto.cerrar}
          </Button>
          <Button variant={estilosLoadingLogin3} onClick={handleClickLogin}>
            {texto.iniciarSesion}
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal show={registrarModal} onHide={handleRegistrarModalClose} backdrop="static" keyboard={false}>
        <Modal.Header>
          <Modal.Title>{texto.registrarse}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div class="form-floating">
            <input type="email" class="form-control" id="floatingInput" placeholder="name@example.com" name="email_register" value={email_register} onChange={handleInputChange} disabled={estilosLoadingRegister1} />
            <label for="floatingInput">{texto.email}</label>
          </div>
          {validatorRegistroCorreo && <div class="navbar-error1">{texto.email_error}</div>}
          {/* Repita Correo */}
          <div class="mb-3"></div>
          <div class="form-floating">
            <input type="email" class="form-control" id="floatingInput" placeholder="name@example.com" name="email_register_confirm" value={email_register_confirm} onChange={handleInputChange} disabled={estilosLoadingRegister1} />
            <label for="floatingInput">{texto.repita_correo}</label>
          </div>
          {validatorRegistroConfirmCorreo && <div class="navbar-error1">{texto.error_confirm_1}</div>}
          {/* Fin Repita Correo */}
          <div class="mb-3"></div>
          <div class="form-floating">
            <input type="password" class="form-control" id="floatingPassword" placeholder="Password" name="password_register" value={password_register} onChange={handleInputChange} disabled={estilosLoadingRegister1} />
            <label for="floatingPassword">{texto.password}</label>
          </div>
          {validatorRegistroContrasenia && <div class="navbar-error1">{texto.error_password}</div>}
          {/* Repita Contraseña */}
          <div class="mb-3"></div>
          <div class="form-floating">
            <input type="password" class="form-control" id="floatingPassword" placeholder="Password" name="password_register_confirm" value={password_register_confirm} onChange={handleInputChange} disabled={estilosLoadingRegister1} />
            <label for="floatingPassword">{texto.repita_contrasenia}</label>
          </div>
          {validatorRegistroConfirmContrasenia && <div class="navbar-error1">{texto.error_confirm_2}</div>}
          {/* Fin Repita Contraseña */}
          <div class="mb-3"></div>

          {loadingRegister && (
            <div class="spinner-border text-primary" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant={estilosLoadingRegister2} onClick={handleRegistrarModalClose}>
            {texto.cerrar}
          </Button>
          <Button variant={estilosLoadingRegister3} onClick={handleClickRegistro}>
            {texto.registrarse}
          </Button>
        </Modal.Footer>
      </Modal>

      <div className="container-fluid">
        <a className="navbar-brand"><b>Salesiana</b>&nbsp;<img src={logo} width="30"/>&nbsp;<b>World</b></a>
        <form className="d-flex">
          <select className="form-select" aria-label="Default select example" value={idioma} onChange={handleIdioma} id="select-idiomas">
            <option data-thumbnail={foto1} value="En">
              English
            </option>
            <option value="Es">Español</option>
            <option value="Fr">Français</option>
            <option value="Rs">Pусский</option>
          </select>
        </form>
        <div>
          {sesionIniciada ? (
            <div>
              <button className="btn btn-outline-danger" onClick={() => handleSesionIniciada(false)}>
                {texto.cerrar_sesion}
              </button>
            </div>
          ) : (
            <div>
              <button className="btn btn-outline-primary me-2" onClick={handleLoginModalShow}>
                {texto.iniciarSesion}
              </button>
              <button className="btn btn-outline-primary" onClick={handleRegistrarModalShow}>
                {texto.registrarse}
              </button>
            </div>
          )}
        </div>
      </div>
    </nav>
  );
};

export default NavbarContainer;
