import { useState } from "react";
import ContentContainer from "./components/content/ContentContainer";
import FoooterContainer from "./components/footer/FoooterContainer";
import NavbarContainer from "./components/navbar/NavbarContainer";
import useStopwatch from "./hooks/useStopwatch";

import "./styles/nicepage.css";
import "./styles/Page-2.css";

function App() {
  const [idioma, setIdioma] = useState("En");
  const [sesionIniciada, setSesionIniciada] = useState(false);
  const [emailSesion, setEmailSesion] = useState("");
  const [haComprado, setHaComprado] = useState(false);

  // metricas

  const [tiempo_de_sesion, tiempo_de_sesion_iniciar_pausar, tiempo_de_sesion_reiniciar] = useStopwatch();

  const handleIdioma = (e) => {
    setIdioma(e.target.value);
  };

  const handleSesionIniciada = (estado) => {
    if (estado) {
      tiempo_de_sesion_iniciar_pausar();
    } else {
      // AQUI ES CUANDO CIERRA SESION
      let ha_comprado = haComprado;
      if (!ha_comprado) {
        const requestOptions = {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ correo: emailSesion }),
        };
        fetch("https://yates-calidad-back.leonelparrales.com/ENoCompra", requestOptions);
      }
      setHaComprado(false);
      tiempo_de_sesion_reiniciar();
    }
    setSesionIniciada(estado);
  };

  return (
    <body className="u-body">
      <NavbarContainer idioma={idioma} handleIdioma={handleIdioma} handleSesionIniciada={handleSesionIniciada} sesionIniciada={sesionIniciada} setEmailSesion={setEmailSesion} />
      <ContentContainer idioma={idioma} sesionIniciada={sesionIniciada} handleSesionIniciada={handleSesionIniciada} setEmailSesion={setEmailSesion} emailSesion={emailSesion} tiempo_de_sesion={tiempo_de_sesion} setHaComprado={setHaComprado}/>
      <FoooterContainer idioma={idioma} tiempo_de_sesion={tiempo_de_sesion} />
    </body>
  );
}

export default App;
