import React, { useEffect, useState } from "react";
import { Modal, Button } from "react-bootstrap";

import useForm from "../../hooks/useForm";
import "./ContentContainer.css";

import validator from "validator";

// Imagenes
import yate1 from "../../images/1.jpg";
import yate2 from "../../images/2.jpg";
import yate3 from "../../images/3.jpg";
import useStopwatch from "../../hooks/useStopwatch";
import ShortCut from "../shortcut/ShortCut";

const ContentContainer = ({ idioma, sesionIniciada, handleSesionIniciada, setEmailSesion, emailSesion, tiempo_de_sesion, setHaComprado }) => {
  // METRICAS
  const [tiempo_demorar_en_comprar, tiempo_demorar_en_comprar_iniciar_pausar, tiempo_demorar_en_comprar_reiniciar] = useStopwatch();

  const [buyModal, setBuyModal] = useState(false);

  const [formValues, handleInputChange, reset] = useForm({
    CCname: "",
    CCnumber: "",
    CCyear: "2021",
    CCmonth: "1",
    CCcvc: "",
    email_login: "",
    password_login: "",
    cantidad: 1,
  });

  const { CCname, CCnumber, CCyear, CCmonth, CCcvc, email_login, password_login, cantidad } = formValues;

  const handleBuyModalShow = (precio) => {
    setPrecio(precio);
    setBuyModal(true);
    tiempo_demorar_en_comprar_iniciar_pausar();
  };
  const [texto, setTexto] = useState({});

  const [loadingLogin, setLoadingLogin] = useState(false);

  // login
  const [validatorLoginCorreo, setValidatorLoginCorreo] = useState(false);
  const [validatorLoginContrasenia, setValidatorLoginContrasenia] = useState(false);
  const [loginModal, setLoginModal] = useState(false);
  const handleLoginModalClose = () => {
    setValidatorLoginCorreo(false);
    setValidatorLoginContrasenia(false);
    reset();
    setLoginModal(false);
  };
  const handleLoginModalShow = () => setLoginModal(true);

  // compra
  const [precio, setPrecio] = useState("100");
  const [id_Producto, setId_Producto] = useState(1);
  const [loadingCompra, setLoadingCompra] = useState(false); // cambiar aquí al final

  // validaciones
  const [validacionCardName, setValidacionCardName] = useState(false);
  const [validacionCardNumber, setValidacionCardNumber] = useState(false);
  const [validacionCardCVC, setValidacionCardCVC] = useState(false);
  const [validacionFecha, setValidacionFecha] = useState(false);

  const handleBuyModalClose = () => {
    tiempo_demorar_en_comprar_reiniciar();
    setValidacionCardName(false);
    setValidacionCardNumber(false);
    setValidacionCardCVC(false);
    setValidacionFecha(false);
    reset();
    setBuyModal(false);
  };

  const handleBuyModalClose_Con_Boton_Close = () => {
    setValidacionCardName(false);
    setValidacionCardNumber(false);
    setValidacionCardCVC(false);
    setValidacionFecha(false);
    reset();
    setBuyModal(false);
    let tiempo = tiempo_demorar_en_comprar;
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ total: tiempo }),
    };
    fetch("https://yates-calidad-back.leonelparrales.com/ECompra", requestOptions);
    tiempo_demorar_en_comprar_reiniciar();
  };

  const condicionalAbrirCompra = (precio, id_del_Producto) => {
    setPrecio(precio);
    setId_Producto(id_del_Producto);
    if (sesionIniciada) {
      handleBuyModalShow(precio);
    } else {
      handleLoginModalShow();
    }
  };

  useEffect(() => {
    if (CCcvc.length > 3) {
      const target = {
        name: "CCcvc",
        value: CCcvc.substring(0, 3),
      };
      handleInputChange({ target });
    }
  }, [CCcvc]);

  useEffect(() => {
    if (CCnumber.length > 16) {
      const target = {
        name: "CCnumber",
        value: CCnumber.substring(0, 16),
      };
      handleInputChange({ target });
    }
  }, [CCnumber]);

  useEffect(() => {
    const target = {
      name: "CCname",
      value: CCname.toUpperCase(),
    };
    handleInputChange({ target });
  }, [CCname]);

  useEffect(() => {
    if (idioma == "En") {
      setTexto({
        titulo: "Luxury Yacht for Sale",
        caracteristicas: {
          yate1: ["Large sundeck", "Semi-displacement yacht", "Northern European quality", "VAT Paid.", "36.89m | 5 Cabns | Year 2008"],
          yate2: ["Large sundeck", "Semi-displacement yacht", "Northern European quality", "VAT Paid.", "38.5m | 5 Cabns | Year 2012"],
          yate3: ["Large sundeck", "Semi-displacement yacht", "Northern European quality", "VAT Paid.", "44.7m | 7 Cabns | Year 2017"],
        },
        sabermas: "Buy Now",
        iniciarSesion: "Sign in",
        email: "Email address",
        password: "Password",
        email_error: "Invalid email.",
        error_password_2: "Enter a valid password.",
        cerrar: "Close",
      });
    } else if (idioma == "Es") {
      setTexto({
        titulo: "Yates de Lujo en Venta",
        caracteristicas: {
          yate1: ["Terraza grande", "Yate de semidesplazamiento", "Calidad del norte de Europa", "IVA incluido.", "36,89m | 5 Cabinas | Año 2008"],
          yate2: ["Terraza grande", "Yate de semidesplazamiento", "Calidad del norte de Europa", "IVA incluido.", "38,5m | 5 Cabinas | Año 2012"],
          yate3: ["Terraza grande", "Yate de semidesplazamiento", "Calidad del norte de Europa", "IVA incluido.", "44,7m | 7 Cabinas | Año 2017"],
        },
        sabermas: "Comprar Ahora",
        iniciarSesion: "Iniciar Sesión",
        email: "Correo electrónico",
        password: "Contraseña",
        email_error: "Correo electrónico inválido.",
        error_password_2: "Ingrese una contraseña válida.",
        cerrar: "Cerrar",
      });
    } else if (idioma == "Rs") {
      setTexto({
        titulo: "Роскошная яхта на продажу",
        caracteristicas: {
          yate1: ["Большая солнечная терраса", "Полуводоизмещающая яхта", "Северо-европейское качество", "НДС уплачен.", "36,89м | 5 кабин | 2008 год"],
          yate2: ["Большая солнечная терраса", "Полуводоизмещающая яхта", "Северо-европейское качество", "НДС уплачен.", "38,5млн | 5 кабин | 2012 год"],
          yate3: ["Большая солнечная терраса", "Полуводоизмещающая яхта", "Северо-европейское качество", "НДС уплачен.", "44,7млн | 7 кабин | 2017 год"],
        },
        sabermas: "Купи сейчас",
        iniciarSesion: "Авторизоваться",
        email: "Адрес электронной почты",
        password: "Пароль",
        email_error: "Неверный адрес электронной почты.",
        error_password_2: "Введите действующий пароль.",
        cerrar: "Неисправность",
      });
    } else {
      setTexto({
        titulo: "Yacht de Luxe à vendre",
        caracteristicas: {
          yate1: ["Grande terrasse", "Yacht semi-déplacement", "Qualité nord-européenne", "TVA payée.", "36,89m | 5 Cabanes | Année 2008"],
          yate2: ["Grande terrasse", "Yacht semi-déplacement", "Qualité nord-européenne", "TVA payée.", "38,5m | 5 Cabanes | Année 2012"],
          yate3: ["Grande terrasse", "Yacht semi-déplacement", "Qualité nord-européenne", "TVA payée.", "44,7m | 7 Cabanes | Année 2017"],
        },
        sabermas: "acheter maintenant",
        iniciarSesion: "S'identifier",
        email: "Adresse e-mail",
        password: "Mot de passe",
        email_error: "Email invalide.",
        error_password_2: "Entrez un mot de passe valide.",
        cerrar: "Fermer",
      });
    }
  }, [idioma]);

  let estilosLoadingLogin1 = "";
  let estilosLoadingLogin2 = "outline-danger";
  let estilosLoadingLogin3 = "primary";

  if (loadingLogin) {
    estilosLoadingLogin1 += "disabled";
    estilosLoadingLogin2 += " disabled";
    estilosLoadingLogin3 += " disabled";
  }

  let estilosVentaSelect = "";
  if (loadingCompra) {
    estilosVentaSelect += "disabled";
  }

  const handleClickLogin = async () => {
    let bandera = true;
    if (!validator.isEmail(email_login)) {
      setValidatorLoginCorreo(true);
      bandera = false;
    } else {
      setValidatorLoginCorreo(false);
    }
    if (password_login.trim().length < 1) {
      setValidatorLoginContrasenia(true);
      bandera = false;
    } else {
      setValidatorLoginContrasenia(false);
    }
    if (bandera) {
      setLoadingLogin(true);
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ correo: email_login, contrasenia: password_login }),
      };
      const response = await fetch("https://yates-calidad-back.leonelparrales.com/login", requestOptions);
      const data = await response.json();
      if (!data[0]) {
        alert("Usuario o Contraseña Incorrecta");
      } else {
        handleSesionIniciada(true);
        setEmailSesion(email_login);
        handleLoginModalClose();
        handleBuyModalShow(precio);
      }
      reset();
      setLoadingLogin(false);
    }
  };

  const realizarCompra = async () => {
    let validacion = true;

    if (CCname.trim().length < 4) {
      validacion = false;
      setValidacionCardName(true);
    } else {
      setValidacionCardName(false);
    }

    if (!validator.isCreditCard(CCnumber)) {
      validacion = false;
      setValidacionCardNumber(true);
    } else {
      setValidacionCardNumber(false);
    }

    if (CCcvc.trim().length < 3) {
      validacion = false;
      setValidacionCardCVC(true);
    } else {
      setValidacionCardCVC(false);
    }

    let mes_procesado = CCmonth;
    if (CCmonth.trim().length === 1) {
      mes_procesado = `0${mes_procesado}`;
    }

    if (validator.isBefore(`${CCyear}-${mes_procesado}-31`)) {
      validacion = false;
      setValidacionFecha(true);
    } else {
      setValidacionFecha(false);
    }

    if (validacion) {
      setLoadingCompra(true);
      setHaComprado(true);
      let requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ id_prod: id_Producto, correo: emailSesion, cantidad: cantidad, total: cantidad * precio }),
      };
      await fetch("https://yates-calidad-back.leonelparrales.com/insertar", requestOptions);
      let tiempo = tiempo_demorar_en_comprar;
      requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ total: tiempo }),
      };
      fetch("https://yates-calidad-back.leonelparrales.com/ETiempoCompra", requestOptions);
      const requestOptions_2 = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ total: tiempo_de_sesion }),
      };
      fetch("https://yates-calidad-back.leonelparrales.com/ECompraCompleta", requestOptions_2);

      tiempo_demorar_en_comprar_reiniciar();
      alert("Compra realizada satisfactoriamente!");
      handleBuyModalClose();
      setLoadingCompra(false);
    }
  };

  return (
    <section class="u-align-center u-clearfix u-palette-4-base u-section-1" id="sec-cd78">
      <ShortCut ejecutar={() => condicionalAbrirCompra("250", 1)}/>
      <Modal show={loginModal} onHide={handleLoginModalClose} backdrop="static" keyboard={false}>
        <Modal.Header>
          <Modal.Title>{texto.iniciarSesion}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div class="form-floating">
            <input type="email" class="form-control" id="floatingInput" placeholder="name@example.com" name="email_login" value={email_login} onChange={handleInputChange} disabled={estilosLoadingLogin1} />
            <label for="floatingInput">{texto.email}</label>
          </div>
          {validatorLoginCorreo && <div class="navbar-error1">{texto.email_error}</div>}
          <div class="mb-3"></div>
          <div class="form-floating">
            <input type="password" class="form-control" id="floatingPassword" placeholder="Password" name="password_login" value={password_login} onChange={handleInputChange} disabled={estilosLoadingLogin1} />
            <label for="floatingPassword">{texto.password}</label>
          </div>
          {validatorLoginContrasenia && <div class="navbar-error1">{texto.error_password_2}</div>}
          <div class="mb-3"></div>

          {loadingLogin && (
            <div class="spinner-border text-primary" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant={estilosLoadingLogin2} onClick={handleLoginModalClose}>
            {texto.cerrar}
          </Button>
          <Button variant={estilosLoadingLogin3} onClick={handleClickLogin}>
            {texto.iniciarSesion}
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal show={buyModal} onHide={handleBuyModalClose} backdrop="static" keyboard={false}>
        <Modal.Header>
          <Modal.Title>Comprar</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div class="row mb-4 mt-2 text-center">
            <div>
              <b>Precio Unitario: </b>${precio}
            </div>
          </div>

          <div class="row mb-3">
            <div class="form-group col-sm-4">
              <label for="ccmonth" class="form-label">
                Cantidad:
              </label>
            </div>
            <div class="form-group col-sm-8">
              <select class="form-control disabled" id="cantidad" name="cantidad" value={cantidad} onChange={handleInputChange} disabled={estilosVentaSelect}>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
                <option value="11">11</option>
                <option value="12">12</option>
              </select>
            </div>
          </div>

          <div class="mb-3">
            <b>Tarjeta de Crédito.</b> Ingrese los detalles de su tarjeta
          </div>

          <div class="mb-3">
            <label for="floatingInput" class="form-label">
              Nombre
            </label>
            <input type="email" class="form-control" id="floatingInput" placeholder="Ingrese su nombre" autocomplete="off" name="CCname" onChange={handleInputChange} value={CCname} disabled={estilosVentaSelect} />
            {validacionCardName && <div class="navbar-error1">Nombre Incorrecto</div>}
          </div>

          <div class="mb-3">
            <label for="ccnumber" class="form-label">
              Número de Tarjeta de Crédito
            </label>
            <div class="input-group">
              <input class="form-control" type="number" placeholder="0000 0000 0000 0000" autocomplete="off" name="CCnumber" onChange={handleInputChange} value={CCnumber} disabled={estilosVentaSelect} />

              <div class="input-group-append">
                <span class="input-group-text">
                  <i class="mdi mdi-credit-card"></i>
                </span>
              </div>
            </div>
            {validacionCardNumber && <div class="navbar-error1">Número de Tarjeta de Crédito Incorrecto</div>}
          </div>

          <div class="row">
            <div class="form-group col-sm-4">
              <label for="ccmonth" class="form-label">
                Mes
              </label>
              <select class="form-control" id="ccmonth" name="CCmonth" value={CCmonth} onChange={handleInputChange} disabled={estilosVentaSelect}>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
                <option value="11">11</option>
                <option value="12">12</option>
              </select>
              {validacionFecha && <div class="navbar-error1">Fecha Incorrecta</div>}
            </div>
            <div class="form-group col-sm-4">
              <label for="ccyear" class="form-label">
                Año
              </label>
              <select class="form-control" id="ccyear" name="CCyear" value={CCyear} onChange={handleInputChange} disabled={estilosVentaSelect}>
                <option value="2021">2021</option>
                <option value="2022">2022</option>
                <option value="2023">2023</option>
                <option value="2024">2024</option>
                <option value="2025">2025</option>
                <option value="2026">2026</option>
              </select>
            </div>
            <div class="col-sm-4">
              <div class="form-group">
                <label for="cvv" class="form-label">
                  CVV/CVC
                </label>
                <input class="form-control" id="cvv" type="number" placeholder="123" autocomplete="off" name="CCcvc" onChange={handleInputChange} value={CCcvc} disabled={estilosVentaSelect} />
                {validacionCardCVC && <div class="navbar-error1">CVV/CVC Incorrecto</div>}
              </div>
            </div>
          </div>

          <div class="row text-end mt-3">
            <div>
              <b>Total:</b> ${precio * cantidad}
            </div>
          </div>

          {loadingCompra && (
            <div class="spinner-border text-primary" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="outline-danger" onClick={handleBuyModalClose_Con_Boton_Close} disabled={estilosVentaSelect}>
            Cerrar
          </Button>
          <Button variant="primary" onClick={realizarCompra} disabled={estilosVentaSelect}>
            Comprar
          </Button>
        </Modal.Footer>
      </Modal>
      <div class="u-clearfix u-sheet u-valign-middle u-sheet-1">
        <h1 class="u-custom-font u-font-oswald u-text u-text-1 animate__animated animate__tada">{texto.titulo}</h1>
        <div class="u-list u-list-1">
          <div class="u-repeater u-repeater-1">
            <div class="u-container-style u-list-item u-repeater-item u-white u-list-item-1">
              <div class="u-container-layout u-similar-container u-valign-top u-container-layout-1">
                <h4 class="u-align-center u-custom-font u-font-montserrat u-text u-text-palette-4-base u-text-2">The Devocean</h4>
                <img alt="" class="u-expanded-width u-image u-image-default u-image-1 zoom-photos" src={yate1} />
                <p class="u-align-center u-text u-text-default u-text-3">{texto.caracteristicas?.yate1[4]}</p>
                <ul class="u-align-left u-custom-list u-text u-text-default u-text-4">
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[0]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[1]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[2]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[3]}
                  </li>
                </ul>
                <div class="agrandar">
                  <a onClick={() => condicionalAbrirCompra("250", 1)} class="u-btn u-btn-rectangle u-button-style u-palette-4-base u-btn-1 agrandar">
                    {texto.sabermas}
                  </a>
                </div>
              </div>
            </div>
            <div class="u-container-style u-list-item u-repeater-item u-video-cover u-white u-list-item-2">
              <div class="u-container-layout u-similar-container u-valign-top u-container-layout-2">
                <h4 class="u-align-center u-custom-font u-font-montserrat u-text u-text-palette-4-base u-text-5">Oceana</h4>
                <img alt="" class="u-expanded-width u-image u-image-default u-image-2 zoom-photos" src={yate2} />
                <p class="u-align-center u-text u-text-default u-text-6">{texto.caracteristicas?.yate2[4]}</p>
                <ul class="u-align-left u-custom-list u-text u-text-default u-text-7">
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[0]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[1]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[2]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[3]}
                  </li>
                </ul>
                <div class="agrandar">
                  <a onClick={() => condicionalAbrirCompra("350", 2)} class="u-btn u-btn-rectangle u-button-style u-palette-4-base u-btn-2 agrandar">
                    {texto.sabermas}
                  </a>
                </div>
              </div>
            </div>
            <div class="u-container-style u-list-item u-repeater-item u-video-cover u-white u-list-item-3">
              <div class="u-container-layout u-similar-container u-valign-top u-container-layout-3">
                <h4 class="u-align-center u-custom-font u-font-montserrat u-text u-text-palette-4-base u-text-8">Mozaique</h4>
                <img alt="" class="u-expanded-width u-image u-image-default u-image-3 zoom-photos" src={yate3} />
                <p class="u-align-center u-text u-text-default u-text-9">{texto.caracteristicas?.yate3[4]}</p>
                <ul class="u-align-left u-custom-list u-text u-text-default u-text-10">
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[0]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[1]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[2]}
                  </li>
                  <li>
                    <div class="u-list-icon">
                      <i class="far fa-check-circle"></i>
                    </div>
                    {texto.caracteristicas?.yate1[3]}
                  </li>
                </ul>
                <div class="agrandar">
                  <a onClick={() => condicionalAbrirCompra("400", 3)} class="u-btn u-btn-rectangle u-button-style u-palette-4-base u-btn-3 agrandar">
                    {texto.sabermas}
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContentContainer;
