import React, { useState } from "react";
import ChatBot from "react-simple-chatbot";

const steps = [
  {
    id: "1",
    message: "Hola Bienvenido",
    trigger: "2",
  },
  {
    id: "2",
    message: "Tiene alguna duda",
    trigger: "3",
  },
  {
    id: "3",
    options: [
      { value: 1, label: "SI", trigger: "4" },
      { value: 2, label: "NO", trigger: "17" },
    ],
  },
  {
    id: "4",
    options: [
      { value: 1, label: "¿Qué cruceros se encuentran disponibles?", trigger: "5" },
      { value: 2, label: "¿Qué proceso debo seguir para comprar un boleto?", trigger: "7" },
      { value: 3, label: "¿Qué debo hacer si mi tarjeta no es reconocida?", trigger: "9" },
      { value: 4, label: "¿La selección que realice al escoger un crucero se guardará si cierro sesión?", trigger: "11" },
      { value: 5, label: "¿Puedo cancelar la selección de un crucero si he decido optar por otro?", trigger: "13" },
      { value: 6, label: "¿Puedo realizar el pago de un crucero en 3 pagos?", trigger: "15" },
    ],
  },
  {
    id: "5",
    message: "Se encuentran disponibles 3 cruceros",
    trigger: "6",
  },
  {
    id: "6",
    options: [
      { value: 1, label: "SI", trigger: "4" },
      { value: 2, label: "NO", trigger: "17" },
    ],
  },
  {
    id: "7",
    message:
      "1. Debe registrarse en nuestra plataforma" +
      "\n2. Una vez registrado debe seleccionar el crucero de su gusto" +
      "\n3. Seleccionar la opción de compra" +
      "\n4. Colocar el número de su tarjeta" +
      "\n5. El proceso de comprar se realizará correctamente",
    trigger: "2",
  },
  {
    id: "8",
    options: [
      { value: 1, label: "SI", trigger: "4" },
      { value: 2, label: "NO", trigger: "17" },
    ],
  },
  {
    id: "9",
    message: "Si su tarjeta no es reconocida debe comunicarse con el banco que ofertó dicha tarjeta",
    trigger: "2",
  },
  {
    id: "10",
    options: [
      { value: 1, label: "SI", trigger: "4" },
      { value: 2, label: "NO", trigger: "17" },
    ],
  },
  {
    id: "11",
    message: "Si, pues nuestro portal asegura guardar y respetar las elecciones hecha por usted",
    trigger: "2",
  },
  {
    id: "12",
    options: [
      { value: 1, label: "SI", trigger: "4" },
      { value: 2, label: "NO", trigger: "17" },
    ],
  },
  {
    id: "13",
    message: "Si, por favor póngase en contacto con nosotros para establecer el cambio de crucero",
    trigger: "2",
  },
  {
    id: "14",
    options: [
      { value: 1, label: "SI", trigger: "4" },
      { value: 2, label: "NO", trigger: "17" },
    ],
  },
  {
    id: "15",
    message: "Al momento de cancelar con tarjeta se puede diferir a 3, 6 o 12 meses sin intereses",
    trigger: "2",
  },
  {
    id: "16",
    options: [
      { value: 1, label: "SI", trigger: "4" },
      { value: 2, label: "NO", trigger: "17" },
    ],
  },
  {
    id: "17",
    message: "Gracias por su visita",
    end: true,
  },
];

const ChatBotComponent = () => {
  const [contador, setContador] = useState(0);

  const handleSumar = () => {
    setContador(contador + 1);
  };

  const handleReset = async () => {
    // EAyuda
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ num: contador }),
    };
    const response = await fetch("https://yates-calidad-back.leonelparrales.com/EAyuda", requestOptions);
    const data = await response.json();
    console.log(data);
    setContador(0);
  };

  console.log(contador);

  return (
    <>
      {/* speechSynthesis={{ enable: true, lang: "es" }} */}
      <div onClick={handleSumar}>
        <ChatBot floating="false" steps={steps} handleEnd={handleReset} />
      </div>
    </>
  );
};

export default ChatBotComponent;
